import React,{useState} from 'react'
import {useNavigate} from "react-router-dom"
import {Box,Typography,TextField,Button} from '@mui/material'
import axios from "axios"
import {useDispatch} from "react-redux";
import {authActions} from "../redux/store"
import { toast } from 'react-hot-toast';
function Login() {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const [inputs,setInputs] = useState({
        email:'',
        password:''
    })
    const handleChange = (e) => {
        setInputs((prevState) => ({
            ...prevState, 
            [e.target.name]: e.target.value,
        }));
    }
    const handleSubmit = async (e) =>{
        e.preventDefault();
        try {
 const {data} = await axios.post('/api/login',{email:inputs.email,password:inputs.password})
    if(data.success){
        dispatch(authActions.login());
        localStorage.setItem("userId", data?.user._id);
        toast.success("Login Successfully")
        navigate("/")
    }
        } catch (error) {
            console.log(error);
        }
    }
  return (
    <>
    <form onSubmit={handleSubmit}>
    <Box
    maxWidth={450}
    display="flex"
    flexDirection={"column"}
    alignItems="center"
    justifyContent={"center"}
    margin= "auto"
    marginTop={5}
    boxShadow="10px 10px 20px #ccc"
    padding={3}
    borderRadius={5}
    >
        <Typography variant='h4'
        padding={3}
        textAlign="center"
        sx={{textTransform:"uppercase"}}
        >Login</Typography>
        <TextField placeholder='email' onChange={handleChange} value={inputs.email} name='email' margin='normal' type={"email"} required/>
        <TextField placeholder='password' onChange={handleChange} value={inputs.password} name='password' margin='normal' type={"password"} required/>
        <Button type='submit' sx={{borderRadius:3,marginTop:3}} variant='contained' color='primary'>Submit</Button>
        <Button onClick={()=> navigate("/register")} sx={{borderRadius:3,marginTop:3}}>Not a User ? please Register</Button>
    </Box>
    </form>
    </>
  )
}

export default Login